<!-- 主承载页面 -->
<template>
    <div id="mainPage" style="height:100%;">
        <TabBar/>
        <keep-alive>
            <router-view v-if="$route.meta.keepAlive"/>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"/>
        <audio id="audioBox" preload="auto"></audio>
    </div>
</template>

<script>
    import TabBar from '../public/TabBar';

    export default {
        name: 'Main',
        components: {
            TabBar
        },
    }
</script>


