import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const state = {
    listIndex: -1,
    songList: [],
    songListPages: 0, // 歌曲列表分页数
    playSondId: 0,
    playedSongName: '暂无歌曲播放',
    songLrcData: {}, // 歌词信息
    isSongCanPlay: false, // 是否可以播放歌曲
};

const getters = {
    getListIndex(state){
        return state.listIndex;
    },
    getSongList(state){
        return state.songList;
    },
    getSongListPages(state){
        return state.songListPages;
    },
    getPlaySondId(state){
        return state.playSondId;
    },
    getPlayedSongName(state){
        return state.playedSongName;
    },
    getSongLrcData(state){
        return state.songLrcData; // 歌词数据来源Home.vue
    },
    getIsSongCanPlay(state){
        return state.isSongCanPlay;
    }
};

const mutations = {
    addListIndex(state){
        state.listIndex++;
    },
    reduceListIndex(){
        state.listIndex--;
    },
    setListIndex(state, listIndex){
        state.listIndex = listIndex;
    },
    setSongList(state, songList){
        state.songList = songList;
    },
    setSongListPages(state, songListPages){
        state.songListPages = songListPages;
    },
    setPlaySondId(state, songId){
        state.playSondId = songId;
    },
    setPlayedSongName(state, songName){
        state.playedSongName = songName;
    },
    setSongLrcData(state, songLrcData){
        state.songLrcData = songLrcData;
    },
    setIsSongCanPlay(state, isSongCanPlay){
        state.isSongCanPlay = isSongCanPlay;
    }
};

export default new Vuex.Store({
    state,
    getters,
    mutations
});


