<!-- 框架组件 -->
<template>
    <div id="home">
        <Category @getChild="songTypeIndex" :song-type="songType"/>
        <div class="songs-content">
            <SongsCenter
                :song-size="songSize"
                :song-offset="songOffset"
                :pagination="pagination"
                :current-page-no="currentPageNo"
                @hMusicPlay="hMusicPlay"
                @hPaginHandle="paginHandle"
            />
            <SongInfo :singer-data="singerData" :song-link="songLink" @hListPlaySongs="listPlaySongs"/>
        </div>
        <div class="layer">正在播放：{{playedSongName}}</div>
        <MusicControl @hMusicPlay="hMusicPlay" @hListPlaySongs="listPlaySongs"/>
    </div>
</template>

<script>
    import Category from '../basic/Category';
    import SongsCenter from './SongsCenter';
    import SongInfo from './SongInfo';
    import MusicControl from './MusicControl';

    export default {
        name: 'Home',
        data(){
            return {
                playTingUid: 0, // 获取歌手信息的id
                songType: 1, // 歌曲分类ID，例如：新歌榜单--1、热歌榜单--2......
                songSize: 18, // 每页显示的数量
                songOffset: 0,
                songLink: '', // 歌曲播放链接
                singerData: {}, // 歌手信息数据
                pagination: false, // 是否显示分页控制器
                currentPageNo: 1, // 当前页码
                haveSongsList: true, // 是否有歌曲
            }
        },
        mounted(){
            if(!this.$store.getters.getSongList.length){
                this.getSongList();
            }
            if(this.$route.params.pagination !== undefined){
                this.pagination = this.$route.params.pagination;
            }
        },
        computed: {
            playSondId(){
                return this.$store.getters.getPlaySondId;
            },
            playedSongName(){
                return this.$store.getters.getPlayedSongName;
            }
        },
        methods: {
            /**
             * 获取歌曲列表
             */
            async getSongList(){
                let result = await $.ajax({
                    url: `${this.HOME}method=baidu.ting.billboard.billList&type=${this.songType}&size=${this.songSize}&offset=${this.songOffset}`,
                    type: 'get',
                    cache: false,
                    dataType: 'jsonp',
                });
                let {billboard = {}, song_list = []} = result;
                let {billboard_songnum = 0} = billboard;
                song_list = song_list || [];
                this.haveSongsList = song_list.length;
                this.pagination = true;
                this.$store.commit('setSongList', song_list);
                this.$store.commit('setSongListPages', Math.ceil(billboard_songnum / this.songSize));
            },
            /**
             * 获取歌手信息
             */
            async getSingerInfo(){
                let data = await $.ajax({
                    url: `${this.HOME}method=baidu.ting.artist.getInfo&tinguid=${this.playTingUid}`,
                    type: 'get',
                    cache: false,
                    dataType: 'jsonp',
                });
                this.singerData = data || {};
                if(!data){
                    console.log(`%c 获取歌手信息失败`, "color:#ff0000");
                }
            },
            /**
             * 获取歌曲歌词
             */
            async getSongLrcInfo(){
                let data = await $.ajax({
                    url: `${this.HOME}method=baidu.ting.song.lry&songid=${this.playSondId}`,
                    type: 'get',
                    cache: false,
                    dataType: 'jsonp',
                });
                let {lrcContent = null} = data;
                this.$store.commit('setSongLrcData', lrcContent);
                if(!data){
                    console.log(`%c 获取歌曲歌词信息失败`, "color:#ff0000");
                }
            },
            /**
             * 获取歌曲链接
             */
            async getSongLink(){
                let data = await $.ajax({
                    url: `${this.HOME}method=baidu.ting.song.play&songid=${this.playSondId}`,
                    type: 'get',
                    cache: false,
                    dataType: 'jsonp',
                });
                let {songinfo = {}, bitrate = {}} = data;
                let {lrclink = ''} = songinfo;
                this.songLink = bitrate.file_link || '';
                if(!data){
                    console.log(`%c 获取歌曲链接失败`, "color:#ff0000");
                }
            },
            /**
             * 接收来自Category.vue的数据
             */
            songTypeIndex(data){
                this.songType = data;
                this.songOffset = 0;
                this.pagination = true;
                this.currentPageNo = 1;
                this.getSongList();
            },
            /**
             * 歌曲播放
             */
            hMusicPlay(){
                let songList = this.$store.getters.getSongList;
                let listIndex = this.$store.getters.getListIndex;
                this.$store.commit('setPlaySondId', songList[listIndex].song_id);
                this.playTingUid = songList[listIndex]['ting_uid'];
                this.playTingUid && this.getSingerInfo();
                // this.getSongLrcInfo(); // 接口未有返回值，暂时不调用
                this.getSongLink();
                this.autoShowPlayText(listIndex, songList);
            },
            /**
             * 列表播放歌曲
             * @param type：[next --> ++, prev --> --]
             */
            listPlaySongs(type){
                let songList = this.$store.getters.getSongList;
                let listIndex = this.$store.getters.getListIndex;
                $("#lrcContent").find('li').removeClass('active');
                $("#lrcContent").css('transform', `translateY(0)`);
                if(type === 'next'){
                    this.$store.commit('addListIndex');
                    if(listIndex >= songList.length - 1){
                        this.$store.commit('setListIndex', 0);
                    }
                }else{
                    this.$store.commit('reduceListIndex');
                    if(listIndex < 0){
                        this.$store.commit('setListIndex', 0);
                        return false;
                    }
                }
                this.hMusicPlay();
                this.$store.commit('setIsSongCanPlay', true);
            },
            /**
             * 自动显示正在播放歌曲名称
             */
            autoShowPlayText: function(listIndex, songList){
                let songName = songList[listIndex] && songList[listIndex].title.replace(/[（）《》]/g, '');
                this.$store.commit('setPlayedSongName', songName);
                $('.layer').show();
                let timer = setTimeout(function(){
                    $('.layer').hide();
                    clearTimeout(timer);
                }, 2000);
            },
            /**
             * 分页操作
             */
            paginHandle(type){
                if(type === 'prev'){
                    this.songOffset -= 18;
                    this.currentPageNo--;
                }else{
                    if(this.haveSongsList){
                        this.songOffset += 18;
                        this.currentPageNo++
                    }
                }
                this.getSongList();
            }
        },
        components: {
            Category,
            SongsCenter,
            SongInfo,
            MusicControl
        }
    }
</script>

<style scoped>
    #home {
        height: calc(100% - 45px);
    }

    .songs-content {
        height: calc(100% - 41px);
        overflow: hidden;
        border-top: 2px solid #fff;
        border-bottom: 5px solid #C20C0C;
        -webkit-box-sizing: content-box;
        -moz-box-sizing: content-box;
        box-sizing: content-box;
    }

    .layer {
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        padding: 0 10px;
        max-width: 300px;
        height: 50px;
        text-align: center;
        line-height: 50px;
        color: #fff;
        background-color: #404453;
        letter-spacing: 1px;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
    }
</style>


