<template>
    <div class="category">
        <a href="javascript:void(0);" :class="{active:item.id === Number(songType)}"
           v-for="(item,index) in songTypeList"
           :key="item.id" @click="songTypeClick(item.id)">{{item.text}}</a>
    </div>
</template>

<script>
    export default {
        props: ['songType'],
        name: "Category",
        data(){
            return {
                songTypeList: [
                    {
                        id: 1,
                        text: '新歌榜单'
                    },
                    {
                        id: 2,
                        text: '热歌榜单'
                    },
                    {
                        id: 11,
                        text: '摇滚榜单'
                    },
                    {
                        id: 12,
                        text: '爵士音乐'
                    },
                    {
                        id: 16,
                        text: '流行音乐'
                    },
                    {
                        id: 21,
                        text: '欧美金曲'
                    },
                    {
                        id: 22,
                        text: '经典老歌'
                    },
                    {
                        id: 23,
                        text: '情歌对唱'
                    },
                    {
                        id: 24,
                        text: '影视金曲'
                    },
                    {
                        id: 25,
                        text: '网络歌曲'
                    }
                ]
            }
        },
        methods: {
            songTypeClick(id){
                // 触发Home.vue中的getChild方法，并且传递值
                this.$emit('getChild', id);
            }
        }
    }
</script>

<style scoped>
    .category {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 34px;
        letter-spacing: 2px;
        background-color: #C20C0C;
        border-bottom: 1px solid #C20C0C;
    }

    .category a {
        padding: 0 13px;
        height: 24px;
        line-height: 24px;
        color: #fff;
        font-size: 14px;
        border-radius: 20px;
    }

    .category a.active,
    .category a:hover {
        background-color: #9B0909;
    }
</style>



