<!-- 歌曲列表组件 -->
<template>
    <div class="songlist">
        <ul class="songlist-list-wrap" ref="listWrap">
            <li class="songlist-list" v-for="(item,index) in songList" :key="item.song_id"
                 :class="{active:playSondId === item.song_id}"
                :style="{height: songListItemHeight + 'px'}"
            >
                <div>
                    <span class="name">{{item.title.replace(/[（）《》()]/g, '')}}</span>
                    <span class="author" :title="item.author">{{item.author}}</span>
                </div>
                <div>
                    <a href="javascript:void(0);"
                       :class="['song-play-pause',(playSondId === item.song_id && isSongCanPlay? 'song-played':'')]"
                       @click="playBtnClick(index,$event)">
                    </a>
                </div>
            </li>
        </ul>
        <!-- 歌曲列表分页 -->
        <div class="pagination">
            <!-- 如果songOffset的值不是0，就显示 -->
            <span v-if="songOffset" @click="paginClick('prev')">上一页</span>
            <span v-if="currentPageNo + 1 <= this.$store.getters.getSongListPages" @click="paginClick('next')">下一页</span>
            <span style="color:#000;cursor:text;">当前{{currentPageNo}}页</span>
        </div>
        <!-- 没有歌曲播放 -->
        <div class="song-no-played" :class="{'page-no-played':pagination}" v-if="!songList.length">
            <span>暂无歌曲列表信息</span>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['songSize', 'songOffset', 'pagination', 'currentPageNo'],
        name: "SongsCenter",
        data(){
            return {
                paused: false
            }
        },
        computed: {
            songList(){
                return this.$store.getters.getSongList;
            },
            playSondId(){
                return this.$store.getters.getPlaySondId;
            },
            isSongCanPlay(){
                return this.$store.getters.getIsSongCanPlay;
            },
            songListItemHeight(){
                return (this.$refs.listWrap.offsetHeight / this.songSize);
            }
        },
        methods: {
            playBtnClick(index, $event){
                // 歌曲播放中，点击暂停
                if($event.target.classList.contains('song-played')){
                    this.$store.commit('setIsSongCanPlay', false);

                }else{ // 歌曲暂停中，点击播放
                    if(this.$store.getters.getListIndex === index){
                        this.$store.commit('setIsSongCanPlay', true);
                    }else{
                        this.$store.commit('setIsSongCanPlay', true);
                        $('#lrcContent').find('li').removeClass('active');
                        $('#lrcContent').css('transform', `translateY(0)`);
                        this.$store.commit('setListIndex', index);
                        this.$emit('hMusicPlay');
                    }
                }
            },
            paginClick(type){
                // 触发Home.vue中的事件
                this.$emit('hPaginHandle', type);
            }
        }
    }
</script>
<style scoped>
    .songlist {
        position: relative;
        float: left;
        width: 50%;
        height: 100%;
        letter-spacing: 2px;
        background-color: #F4F4F4;
        border-left: 2px solid #c10d0c;
        border-top: 2px solid #c10d0c;
    }

    .song-no-played {
        top: 0;
        width: 100%;
        height: 95.8%;
    }

    .songlist-list-wrap {
        height: calc(100% - 28px);
    }

    .songlist-list {
        display: flex;
        align-items: center;
        padding: 0 10px;
        white-space: nowrap;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
    }

    .songlist-list .name {
        max-width: 350px;
        float: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .songlist-list .author {
        float: right;
        padding-right: 10px;
        padding-left: 0;
        white-space: nowrap;
        max-width: 150px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .songlist-list:nth-child(odd) {
        background-color: #E8E8E8;
    }

    .songlist-list:nth-child(even) {
        background-color: #F4F4F4;
    }

    .songlist-list.active span,
    .songlist-list.active a {
        color: #C20C0C;
        font-weight: bold;
        font-size: 18px;
    }

    .songlist-list > div {
        float: left;
        white-space: nowrap;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
    }

    .songlist-list > div:first-child {
        position: relative;
        width: 95%;
    }

    .songlist-list > div:last-child {
        width: 5%;
    }

    .songlist-list a {
        display: block;
        width: 100%;
        height: 30px;
    }

    .songlist-list a:hover {
        color: #C20C0C;
    }

    /* 分页 */
    .pagination {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        padding-right: 10px;
        text-align: right;
        line-height: 26px;
        border-top: 2px solid #c10d0c;
        background: #fff;
    }

    .pagination span {
        color: #2699EC;
        font-weight: bold;
        cursor: pointer;
    }
</style>


