<!-- 歌手信息以及歌词信息组件 -->
<template>
    <div class="song-info">
        <div class="song-info-siger">
            <p>歌手信息</p>
            <p v-if="singerData['avatar_s1000']" class="siger-logo">
                <img :src="singerData['avatar_s1000']" alt="歌手照片" width="100"/>
            </p>
            <p v-if="!singerData.avatar_s1000" class="siger-logo">
                <img src="../../../static/images/no-data-icon.png"
                     height="200" width="240" alt="" style="width:50%;height: 50%;"/>
            </p>
            <p v-if="singerData['country']">
                地区：{{/香港|澳门|台湾/g.test(singerData.country)?"中国"+singerData.country:singerData.country}}
            </p>
            <p v-if="singerData['name']">姓名：{{singerData['name']}}</p>
            <p v-if="singerData['company']">公司：{{singerData['company']}}</p>
            <p v-if="singerData['birth']">生日：{{singerData['birth']}}</p>
            <p v-if="singerData['intro']" class="siger-intro">简介：{{singerData['intro']}}</p>
            <!-- 没有歌曲播放 -->
            <div class="song-no-played" v-if="!singerData['ting_uid']">
                <span>暂无歌手信息</span>
            </div>
        </div>
        <div class="song-info-lrc">
            <p>{{songLrcData && songLrcData['title'] ? songLrcData['title'].replace(/[（）《》]/g, '') : '歌词信息'}}</p>
            <!-- 没有歌曲播放 -->
            <div class="song-no-played" v-if="!lrcData.length">
                <span>敬请期待</span>
            </div>
            <div v-else class="lrc-content-wrap">
                <ul id="lrcContent" class="lrc-content">
                    <li v-for="item in lrcData" :data-minutes="item.minutes" :data-seconds="item.seconds">
                        <span>{{item.text}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['singerData', 'songLink'],
        name: "SongInfo",
        data(){
            return {
                lrcData: [],
                autoTimer: null
            }
        },
        computed: {
            songLrcData(){
                return this.$store.getters.getSongLrcData;
            },
            isSongCanPlay(){
                return this.$store.getters.getIsSongCanPlay;
            },
        },
        watch: {
            songLrcData(newVal, oldVal){
                this.lrcData = this.analysisLrc(this.songLrcData);
            },
            songLink(newVal, oldVal){
                if(this.songLink){
                    $('#audioBox')[0].src = this.songLink;
                    $('#audioBox')[0].oncanplay = function(){
                        this.play();
                    };
                }
            },
            isSongCanPlay(newVal, oldVal){
                if(newVal){
                    $('#audioBox')[0].play();
                    this.playing();
                }else{
                    clearInterval(this.autoTimer);
                    $('#audioBox')[0].pause();
                }
            }
        },
        methods: {
            /**
             * 解析歌词
             */
            analysisLrc(data){
                if(!data){
                    return false;
                }
                let ary = [];
                data.replace(/[\r\n\s&nbsp;]/igm, '').replace(/\[(\d+):(\d+)\.\d+]([^\[]+)?/igm, (...arg) => {
                    let [, minutes, seconds, text] = arg;
                    if(text){
                        ary.push({minutes, seconds, text,});
                    }
                });
                return ary;
            },
            /**
             * 歌曲播放以及歌词对应
             */
            playing(){
                clearInterval(this.autoTimer);
                this.autoTimer = setInterval(() => {
                    let duration = $('#audioBox')[0].duration;
                    let curTime = $('#audioBox')[0].currentTime;
                    this.lyricSpondence(duration, curTime);
                    // 播放完成后暂停
                    if(curTime >= duration){
                        clearInterval(this.autoTimer);
                        // 触发Home.vue 的方法
                        this.$emit('hListPlaySongs', 'next');
                    }
                }, 1000);
            },
            /**
             * 歌词对应
             * @param duration
             * @param curTime
             */
            lyricSpondence(duration, curTime){
                let minutes = Math.floor(curTime / 60);
                let seconds = Math.floor(curTime - minutes * 60);
                minutes < 10 ? minutes = '0' + minutes : null;
                seconds < 10 ? seconds = '0' + seconds : null;
                let liList = $('#lrcContent').find('li');
                let curLi = liList.filter(`[data-minutes="${minutes}"]`).filter(`[data-seconds="${seconds}"]`);
                if(curLi.length){
                    curLi.addClass('active').siblings().removeClass('active');
                    let index = curLi.index();
                    if(index <= 8 || index > liList.length - 9){
                        return;
                    }
                    $('#lrcContent').css('transform', `translateY(${(-index + 8) * 30}px)`);
                }
            }
        }
    }
</script>

<style scoped>
    .song-info {
        float: right;
        width: 50%;
        height: 100%;
        letter-spacing: 1px;
        border-left: 2px solid #c10d0c;
    }

    .song-info > div {
        position: relative;
        float: left;
        padding: 0 10px;
        width: 50%;
        height: 100%;
        line-height: 30px;
        text-align: center;
    }

    .song-info-siger > p:first-child {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        color: #fff;
        background-color: #C20C0C;
    }

    .siger-logo {
        margin-top: 50px;
        padding: 5px 0;
    }

    .siger-logo img {
        width: 50%;
        vertical-align: middle;
    }

    .siger-intro {
        height: 210px;
        overflow: hidden;
    }

    .song-info-lrc {
        border-left: 2px solid #C10D0C;
        border-right: 2px solid #c10d0c;
    }

    .song-info-lrc > p {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        background-color: #C20C0C;
        color: #fff;
        overflow: hidden;
        white-space: nowrap;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
    }

    .lrc-content-wrap {
        margin-top: 40px;
        height: 510px;
        overflow: hidden;
    }

    .lrc-content {
        transition: all .3s linear;
    }

    .lrc-content li {
        list-style: none;
        height: 30px;
        white-space: nowrap;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        transition: all .3s linear;
    }

    .lrc-content li span {
        transition: all .3s linear;
    }

    .lrc-content li.active span {
        color: #C20C0C;
        font-weight: bold;
        font-size: 18px;
    }
</style>


