<template>
    <div class="tabbar">
        <router-link to="/main/home">音乐首页</router-link>
        <router-link to="/main/search">音乐搜索</router-link>
    </div>
</template>

<script>
    // 路由跳转
    // params传参 ，路径不能使用path 只能使用name,不然获取不到传的数据
    // query传参
    // name属性值为：路由文件中定义的'name'值
    // this.$router.push({name: 'login', params: {name: 'renmeiling'}

    export default {
        name: 'TabBar'
    }
</script>

<style scoped>
    .tabbar {
        display: flex;
        justify-content: space-around;
        width: 100%;
        height: 45px;
        line-height: 45px;
        letter-spacing: 2px;
        font-size: 16px;
        text-align: center;
        background-color: #404453;
        border-bottom: 1px solid #404453;
    }

    .tabbar a {
        margin: 8px 0;
        padding: 0 19px;
        height: 30px;
        line-height: 30px;
        color: #ccc;
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        border-radius: 15px;
    }

    .tabbar a:hover,
    .tabbar a.active {
        color: #fff;
        background-color: #333;
    }
</style>
<!--
容器属性：
  flex-direction：决定主轴的方向（即项目的排列方向）
  flex-wrap：定义换行情况
  flex-flow：flex-direction和flex-wrap的简写，默认row nowrap
  justify-content：定义项目在主轴上的对齐方式。
  align-item：定义在交叉轴上的对齐方式
  align-content：定义多根轴线的对齐方式

项目属性：
  order：定义项目的排列顺序。
  flex-grow：定义项目的放大比例
  flex-shrink：定义项目的缩小比例
  flex-basis：定义在分配多余空间之前，项目占据的主轴空间。
  flex：flex-grow，flex-shrink和flex-basis的简写
  align-self：允许单个项目与其他项目有不一样的对齐方式
-->


