<!-- 歌曲播放控制器组件 -->
<template>
    <div :class="['music-control', locked ? 'locked' : '']">
        <div class="btns">
            <a href="javascript:void(0);" class="prev" title="上一首" @click="prevClick">上一首</a>
            <a href="javascript:void(0);" :class="['curr', isSongCanPlay ? 'played': '']"
               title="播放/暂停" @click="currClick">播放/暂停</a>
            <a href="javascript:void(0);" class="next" title="下一首" @click="nextClick">下一首</a>
            <p class="curr-song-name">{{playedSongName}}</p>
        </div>
        <div class="lock">
            <span @click="lockClick"></span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "MusicControl",
        data(){
            return {
                locked: false,
            }
        },
        computed: {
            listIndex(){
                return this.$store.getters.getListIndex;
            },
            songList(){
                return this.$store.getters.getSongList;
            },
            playedSongName(){
                return this.$store.getters.getPlayedSongName;
            },
            isSongCanPlay(){
                return this.$store.getters.getIsSongCanPlay;
            },
        },
        methods: {
            lockClick(){
                this.locked = !this.locked;
            },
            currClick($event){
                if($event.target.classList.contains('played')){
                    this.$store.commit('setIsSongCanPlay', false);
                }else{
                    this.$store.commit('setIsSongCanPlay', true);
                    if(this.listIndex < 0){
                        this.$store.commit('setListIndex', 0);
                    }
                    !document.getElementById("audioBox").src && this.$emit("hMusicPlay");
                }
            },
            // 上一首歌曲
            prevClick(){
                if(this.listIndex === 0){
                    return false;
                }
                this.$emit("hListPlaySongs", "prev");
            },
            // 下一首歌曲
            nextClick(){
                if(this.listIndex >= this.songList.length - 1){
                    return false;
                }
                this.$emit("hListPlaySongs", "next");
            }
        }
    }
</script>

<style scoped>
    .music-control {
        position: fixed;
        left: -100%;
        bottom: 0;
        width: 100%;
        height: 53px;
        letter-spacing: 2px;
        -moz-transition: all 0.3s linear;
        -webkit-transition: all 0.3s linear;
        -o-transition: all 0.3s linear;
        transition: all 0.3s linear;
        background: url("../../../static/images/playbar.png") repeat-x 0 0;
    }

    .music-control.locked,
    .music-control:hover {
        left: 0;
    }

    .music-control.locked .lock,
    .music-control:hover .lock {
        top: -14px;
        right: 0;
        transform: rotate(0);
    }

    .music-control .btns {
        margin: 12px auto 0;
        width: 600px;
        overflow: hidden;
    }

    .music-control a {
        display: block;
        float: left;
        width: 28px;
        height: 28px;
        margin-right: 8px;
        margin-top: 5px;
        text-indent: -9999px;
        background: url("../../../static/images/playbar.png") no-repeat;
    }

    .music-control .prev {
        background-position: 0 -130px;
    }

    .music-control .prev:hover {
        background-position: -30px -130px;
    }

    .music-control .curr {
        width: 36px;
        height: 36px;
        margin-top: 0;
        background-position: 0 -204px;
    }

    .music-control .curr:hover {
        background-position: -40px -204px;
    }

    .music-control .curr.played {
        background-position: 0 -165px;
    }

    .music-control .next {
        background-position: -80px -130px;
    }

    .music-control .next:hover {
        background-position: -110px -130px;
    }

    .music-control .lock {
        position: absolute;
        top: -2px;
        right: -14px;
        width: 52px;
        height: 67px;
        transform: rotate(90deg);
        background: url("../../../static/images/playbar.png") no-repeat 0 -380px;
    }

    .music-control .lock span {
        display: block;
        width: 18px;
        height: 18px;
        margin: 6px 0 0 17px;
        background: url("../../../static/images/playbar.png") no-repeat -80px -380px;
        cursor: pointer;
    }

    .music-control .lock span:hover {
        background-position: -80px -400px;
    }

    .music-control.locked .lock span {
        background-position: -100px -380px;
    }

    .music-control.locked .lock span:hover {
        background-position: -100px -400px;
    }

    .music-control .curr-song-name {
        float: left;
        line-height: 40px;
        color: #fff;
    }
</style>


